<template>
  <div class="personal-container">
    <div class="tuoyuan-top"></div>
    <div class="userInfo-container">
      <div class="userInfo-box">
        <div class="avator-box"></div>
        <div class="user-name">举个栗子</div>
        <div class="money-list">
          <div class="money-task">
            <div class="money-list-title">任务佣金(元)</div>
            <span>0.00</span>
          </div>
          <div class="money-freeze">
            <div class="money-list-title">冻结佣金(元)</div>
            <span>0.00</span>
          </div>
          <div class="money-balance">
            <div class="money-list-title">可提现余额(元)</div>
            <span>0.00</span>
          </div>
        </div>
      </div>
      <div class="user-menu clearfix">
        <span class="fl user-title">金牌用户</span>
        <span class="fr user-more-btn">点击查看</span>
      </div>
    </div>
    <div class="users-management">
      <Title1
        title="用户管理"
        subTitle="User Manage"
      />
      <div class="icons-container clearfix">
        <icon-link-item
          class="fl"
          :class="{mr20:index!==(manageList.length-1)}"
          v-for="( item, index ) in manageList"
          :key="index"
          :itemType="item.type"
          :title="item.title"
          :iconUrl="item.iconUrl"
        />
      </div>
    </div>
    <div class="gg-container"></div>
    <div class="personal-serve">
      <Title1
        title="个人服务"
        subTitle="Personal service"
      />
      <div class="icons-container clearfix">
        <icon-link-item
          class="fl"
          :class="{mr20:index%4!==3}"
          v-for="( item, index ) in pserviceList"
          :key="index"
          :itemType="item.type"
          :title="item.title"
          :iconUrl="item.iconUrl"
        />
      </div>
    </div>
  </div>
</template>

<script>
  import IconLinkItem from '../../components/item/icon-link-item.vue'
  import { Title1 } from '../layout/components'
  import * as icons from '../../iconLink/personal.js'
  export default {
    name: 'name',
    data() {
      return {
        manageList: [
          {
            type: 'wait',
            title: '普通用户',
            iconUrl: icons.SIMPLEUSER
          },
          {
            type: 'wait',
            title: '盟主',
            iconUrl: icons.LEADER
          },
          {
            type: 'wait',
            title: '广告主',
            iconUrl: icons.GGBOSS
          },
          {
            type: 'wait',
            title: '商户',
            iconUrl: icons.SHOPBOSS
          }
        ],
        pserviceList: [
          {
            type: 'wait',
            title: '运营统计',
            iconUrl: icons.SIMPLEUSER
          },
          {
            type: 'wait',
            title: '商户管理',
            iconUrl: icons.LEADER
          },
          {
            type: 'wait',
            title: '层级关系',
            iconUrl: icons.GGBOSS
          },
          {
            type: 'wait',
            title: '资金明细',
            iconUrl: icons.SHOPBOSS
          },
          {
            type: 'wait',
            title: '常见问题',
            iconUrl: icons.SIMPLEUSER
          },
          {
            type: 'wait',
            title: '意见反馈',
            iconUrl: icons.LEADER
          },
          {
            type: 'wait',
            title: '检查更新',
            iconUrl: icons.GGBOSS
          }
        ]
      }
    },
    components: {
      IconLinkItem,
      Title1
    }
  }
</script>

<style scoped>

</style>



